//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';

export default {
	data() {
		return {
			background:{
				backgroundColor: '#fff',
			},
			taskNum:0,
			taskingNum:0,
			getMoney:0,
			payMoney:0
		}
	},
	
	onLoad() {
		this.InitData()
	},
	
	methods: {
		
		
		InitData(){
			// 发送到服务端
			$H.get("/taskInit").then((res) => {
				//请求成功
				console.log(res)
				this.taskNum=res.data.totalTask
				this.taskingNum=res.data.totalCommission
				this.getMoney=res.data.wati_acount
				this.payMoney=res.data.totalPay
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		toYongJin(){
			uni.navigateTo({
				url:'../woDeYongJin/woDeYongJin'
			})
		},
		
		toShopTask(){
			uni.navigateTo({
				url:'../shopTask/shopTask'
			})
		},
		toYaoYue(){
			uni.navigateTo({
				url:'../tuiJianYaoYue/tuiJianYaoYue'
			})
		},
		
		toFaBu(){
			uni.navigateTo({
				url:'../fabuTask/fabuTask'
			})
		},
		
		toRenWu(){
			uni.navigateTo({
				url:'../faBuRenWu/faBuRenWu'
			})
		}
	}
}
